<template>
  <div class="search-panel">
    <div class="search-nav">
      <div class="search-input">
        <img src="../images/search.png" alt="" width="25">
        <input type="search" ref="mySearch" placeholder="华为手机">
      </div>
      <button @click="showSearchPanel(false)">取消</button>
    </div>
    <div class="search-content">
      <div class="title">
        <img src="../images/hot.png" alt="" width="25">
        <span>热门搜索</span>
      </div>
      <ul class="search-list">
        <li>面巾纸</li>
        <li>浴巾儿童</li>
        <li>石榴水果</li>
        <li>冰箱保鲜盒</li>
        <li>富豪卷纸</li>
        <li>毛巾浴袍</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "SearchPanel",
  props: {
    showSearchPanel: Function
  },
  mounted() {
    this.$refs.mySearch.focus()
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../../../common/stylus/mixins.styl"
  .search-panel
    width 100%
    height 100%
    background-color #fff
    position fixed
    left 0
    top 0
    z-index 1000
    .search-nav
      width 100%
      height 60px
      padding 0 20px
      border-bottom-1px(#ddd)
      display flex
      flex-direction row
      justify-content space-between
      align-items center
      .search-input
        width 85%
        height 38px
        border-radius 5px
        background-color #ededed
        padding 0 10px
        display flex
        flex-direction row
        align-items center
        input
          width 90%
          height 90%
          padding-left 10px
          background-color transparent
          color #aaa
          font-size 16px
          outline none
      button
        background-color transparent
        font-size 16px
        color #aaa
    .search-content
      padding 30px 15px
      .title
        color #aaa
        margin-bottom 10px
        display flex
        flex-direction row
        align-items center
      .search-list
        display flex
        flex-direction row
        flex-wrap wrap
        li
          background-color #ededed
          color #aaa
          margin 5px 15px
          padding 5px
          border-radius 10px
          font-weight lighter
          font-size 14px

</style>



